.assess {
   position: relative;
   padding: 60px 0;
   @include xs {
      padding: 60px 0 0; }
   &::after {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      height: 130px;
      background-color: #D5B893;
      content: '';
      @include xs {
         bottom: -30px; } }
   &__title {
      max-width: 650px;
      margin: 0 auto 107px;
      @include sm {
         margin: 0 auto 60px; }
      @include xs {
         margin: 0 auto 20px; }
      .section-title__descr {
         @include xs {
            letter-spacing: 0; } } }
   &__content {
      position: relative;
      max-width: 994px;
      margin: 0 auto;
      padding: 61px 60px 57px;
      box-shadow: 0 5px 12px rgba(0, 0, 0, 0.04);
      border: 1px solid #d5b893;
      background-color: #fff;
      @include xs {
         padding: 25px 20px 20px; }
      &::before {
         position: absolute;
         top: -50px;
         left: -104px;
         right: -104px;
         height: 434px;
         z-index: -1;
         background-color: #513A1D;
         content: '';
         @include xs {
            display: none; } }
      &-title {
         max-width: 610px;
         margin: 0 auto 30px;
         @include f('pn700', 24, 25, 0);
         color: #6D788C;
         text-align: center;
         @include xs {
            font-size: 20px;
            line-height: 22px; } } }

   &__form {
      width: 562px;
      @include xs {
         width: auto; }
      &-block {
         $parent: &;
         &_width-1 {
            max-width: 518px;
            @include xs {
               max-width: none; } }
         &_width-3 {
            max-width: 358px;
            @include xs {
               max-width: none; } }
         & + & {
            margin-top: 44px;
            @include xs {
               margin-top: 30px; } }
         &-title {
            margin-bottom: 30px;
            @include f('pn600', 20, 25, 0);
            color: #21262D;
            @include xs {
               margin-bottom: 15px;
               font-size: 18px;
               line-height: 22px; } }
         &-row {
            display: flex;
            margin: 0 -15px;
            @include xs {
               flex-wrap: wrap; }
            #{$parent}_width-3 & {
               margin: 0 -10px; } }
         &-col {
            flex: 0 1 50%;
            width: 50%;
            padding: 0 15px;
            #{$parent}_width-1 & {
               &:nth-child(1) {
                  flex: 0 1 45%;
                  width: 45%;
                  @include xs {
                     flex: 0 1 auto;
                     width: 100%; } }
               &:nth-child(2) {
                  flex: 0 1 55%;
                  width: 55%;
                  @include xs {
                     flex: 0 1 auto;
                     width: 100%; } } }
            #{$parent}_width-2 & {
               padding: 0 10px;
               @include xs {
                  padding: 0 15px; }
               &:nth-child(1) {
                  flex: 0 0 50.5%;
                  width: 50.5%;
                  @include xs {
                     flex: 0 1 auto;
                     width: 100%; } }
               &:nth-child(2) {
                  flex: 0 1 50%;
                  width: 50%;
                  @include xs {
                     flex: 0 1 auto;
                     width: 100%; } } }
            &:not(:last-child) {
               @include xs {
                  margin-bottom: 10px; } } } }
      &-check-box {
         &:not(:last-child) {
            margin-bottom: 10px; } }
      &-actions {
         margin-top: 30px;
         @include sm {
            margin-top: 20px; }
         &_width-3 {
            max-width: 358px;
            @include xs {
               max-width: none; } } }
      &-submit {
         width: 100%;
         margin-bottom: 26px; }
      &-check {} }

   &__result {
      position: absolute;
      top: 145px;
      right: -45px;
      width: 358px;
      border: 22px solid #f9f2ea;
      opacity: 0;
      transform: translateY(100px);
      @include md {
         right: 20px;
         border-width: 15px; }
      @include sm {
         position: static;
         margin: 0 0 20px; }
      @include xs {
         width: auto;
         margin: 0 -20px 20px; }
      &-inner {
         box-shadow: 0 5px 10px rgba(0, 0, 0, 0.04);
         background-color: #ffffff; }
      &-header {
         padding: 20px 25px;
         @include xs {
            padding: 15px; } }
      &-descr {
         max-width: 170px;
         @include f('pn400', 16, 20);
         color: #21262D; }
      &-title {
         padding: 0 25px;
         margin-bottom: 20px;
         letter-spacing: .5px;
         @include xs {
            padding: 0 20px; } }
      &-item {}
      &-text {
         display: flex;
         align-items: flex-start;
         padding: 25px;
         @include xs {
            padding: 15px; }
         &:nth-child(odd) {
            background-color: #F9F9F9; }
         &-title {
            flex: 0 0 110px;
            padding-right: 10px;
            @include f('pn700', 16, 25);
            letter-spacing: .5px;
            color: #21262D; }
         &-descr {
            @include f('pn600', 14, 18, 0);
            color: #808A9D;
            @include xs {
               font-size: 12px;
               line-height: 14px; } } } } }
