.screen {
   &__content {
      position: relative;
      padding: 220px 0 140px;
      @include sm {
         padding: 170px 0 140px; }
      @include xs {
         padding: 140px 0 140px; }
      &::after {
         position: absolute;
         top: 100%;
         left: 0;
         width: 100%;
         height: 121px;
         background-color: #D5B893;
         content: ''; } }
   &__title {
      max-width: 900px;
      margin: 0 auto 30px; }
   &__container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      text-align: center; }
   &__btn {
      min-width: 410px;
      @include xs {
         min-width: 0; } }
   &__benefits {
      position: relative;
      z-index: 1;
      max-width: 1200px;
      margin: -73px auto 0; } }

.benefits {
   background: $white;
   border: 1px solid #DCB385;
   box-shadow: 0 5px 10px rgba(0, 0, 0, 0.04);
   @include xs {
      position: relative; }
   &__row {
 }      //margin: 0 -10px
   &__item {
      $parent: &;
      position: relative;
      padding: 50px 15px 60px;
      text-align: center;
      line-height: 17px;
      color: #363C43;
      &:not(:last-child) {
         @include xs {
            padding: 50px 15px; }
         &::after {
            position: absolute;
            top: calc(50% - 102px);
            right: 0;
            width: 12px;
            height: 205px;
            background: url("../img/decor/vert-line.svg") no-repeat;
            content: '';
            @include xs {
               top: auto;
               bottom: -160px;
               right: calc(50% - 50px);
               transform: rotate(90deg);
               background-size: contain;
               width: 100px;
               height: 250px;
               pointer-events: none; } } }
      .diamond-shape__title {
         position: relative;
         top: 5px; }
      &:nth-child(2) {
         &::after {
            right: -10px;
            @include md {
               right: 0; }
            @include sm {
               right: -10px; } }
         .diamond-shape__title {
            font-size: 48px; } }

      &-diamond {
         margin-bottom: 23px; }
      &-descr {
         max-width: 280px;
         margin: 0 auto;
         @include f('pn400', 14, 17);
         #{$parent}:nth-child(1) & {
            max-width: 240px; } }
      &-content {
         pointer-events: none;
         opacity: 0;
         transform: scale(.7);
         position: absolute;
         top: -1px;
         left: -15px;
         right: -15px;
         bottom: -1px;
         z-index: 5;
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
         background: #fafafa;
         border: 1px solid #dcb385;
         transition: .3s ease-in-out;
         content: '';
         @include sm {
            cursor: pointer; }
         @include xs {
            left: 14px;
            right: 14px; }
         #{$parent}:nth-child(1) & {
            left: 14px; }
         #{$parent}:nth-child(3) & {
            right: 12px;
            @include xs {
               right: 14px; } }

         #{$parent}:hover & {
            pointer-events: auto;
            opacity: 1;
            transform: scale(1); }
         &-text {
            max-width: 270px;
            margin-bottom: 40px;
            @include f('man500', 16, 22);
            @include sm {
               max-width: 220px;
               font-size: 14px;
               line-height: 20px; } }

         &-link {
            @include f('man500');
            color: #d5b893;
            @include sm {
               font-size: 13px; } } } } }
